.google-button {
    height: 40px;
    border-width: 0;
    background: white;
    color: #737373;
    border-radius: 5px;
    white-space: nowrap;
    box-shadow: 1px 1px 0px 1px rgba(0,0,0,0.1);
    transition-property: background-color, box-shadow;
    transition-duration: 150ms;
    transition-timing-function: ease-in-out;
    padding: 0;
    position: fixed;
    top: 5px; /* Adjust the top distance as needed */
    right: 20px;
    transform: scale(1.4);
    
  }
  .google-button:hover, .google-button:focus{
    box-shadow: 1px 4px 5px 1px rgba(0,0,0,0.2);
    outline: none;
  transform: scale(1.4) skewX(-0.4deg);
  }
  .google-button:active{
    outline: none;
    box-shadow: 1px 4px 5px 1px rgba(0,0,0,0.3);
    transition-duration: 10ms;
  }
  .google-button__icon {
    display: inline-block;
    vertical-align: middle;
    margin: 0px 0 8px 8px;
    width: 18px;
    height: 18px;
    box-sizing: border-box;
  }
  .google-button__icon--plus {
    width: 27px;
  }
  .google-button__text {
    display: inline-block;
    vertical-align: middle;
    padding: 0 24px;
    font-size: 14px;
    font-weight: bold;
    font-family: 'Roboto',arial,sans-serif;
  }
  .user-initials {
    height: 40px;
    width: 40px;
    border-radius: 50%;
    background-color: #4285f4; /*  background color */
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 18px;
    font-weight: bold;
    cursor: pointer;
    margin-right: 10px; /*  margin */
  }
  
  .user-initials:hover {
    box-shadow: 1px 4px 5px 1px rgba(0,0,0,0.2);
    transform: scale(1.1); /*  hover effect */
  }